import uniIcon from '@/components/uni-icon/uni-icon'
export default {
	props: {
		icon: String,
		placeholder: String,
		type: {type: String, default: 'text'},
		sms: Boolean,
		count: Number,
	},
	model: {
		prop: 'value',
		event: 'input'
	},
	name: '',
	data () {
		return {
			value: '',
		}
	},
	computed: {
		
	},
	onLoad () {

	},
	methods: {
		onInputEvent (e) {
			this.value = e.target.value;
			this.$emit('input', e.target.value)
		},
		onClearEvent () {
			this.value = '';
			this.$emit('input', '')
		},
		onGetSmsEvent () {
			this.$emit('onGetSmsEvent')
		}
	},
	components: {
		uniIcon
	}
}
